:root {
  --background: #950740;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #1a1a1d;
  font-family: "Work Sans", sans-serif;

  font-family: "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content {
  height: 500px;
  background-color: #1a1a1d; /* second color */
  background-blend-mode: multiply;
  background-size: cover;
  display: grid;
  place-items: center;
}

/* navigation styles start here */

header {
  background: var(--background);
  text-align: center;
  position: fixed;
  z-index: 999;
  width: 100%;
  top: 0;
}

/* makes it tabbale */
.nav-toggle {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important;
}

.nav-toggle:focus ~ .nav-toggle-label {
  outline: 3px solid rgba(lightblue, 0.75);
}

.nav-toggle-label span::before {
  bottom: 7px;
}

.nav-toggle-label span::after {
  top: 7px;
}

nav {
  position: absolute;
  top: 100%;
  background: var(--background);
  width: 100%;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  align-items: center;
}
.logo1 {
  margin-top: 68px;
}
.content {
  height: auto;
  background-color: #1a1a1d; /* second color */
  background-blend-mode: multiply;
  background-size: cover;
  display: flex;
  justify-content: center;
}
.container1 {
  display: flex;
  flex-wrap: wrap;
  max-width: 900px;
  padding: 20px;
  align-items: flex-start;
  justify-content: flex-start;
}
.container1 > * {
  margin: 10px;
  flex: 1 1;
  min-width: 260px;
  max-width: 260px;
}

.color {
  color: blue;
}
.p {
  margin: 0;
  padding: 10px;
}
.center {
  display: flex;

  text-align: center;
}

.mediatest {
  height: 0;
  padding-top: 80%;
  object-fit: cover;
}

.footer {
  position: relative;
  padding-top: 100px;
  overflow: hidden;
}

.top,
.bottom {
  position: relative;
  height: 50px;
}

.bottom {
  margin-top: 10px;
}

.top::before {
  -webkit-transform-origin: left top;
          transform-origin: left top;
  -webkit-transform: skew(45deg);
          transform: skew(45deg);
  position: absolute;
  background: #6b7684;
  height: 100px;
  width: 145px;
  content: "";
  top: 100%;
  right: 0;
}

.bottom:before {
  -webkit-transform-origin: right bottom;
          transform-origin: right bottom;
  -webkit-transform: skew(45deg);
          transform: skew(45deg);
  position: absolute;
  background: var(--background);
  height: 150px;
  bottom: 100%;
  width: 95px;
  content: "";
  left: 0;
}
footer {
  position: absolute;
  width: 100%;
  height: 100px;
  bottom: 0;
  overflow: hidden;
}
.top::after,
.bottom::after {
  -webkit-transform-origin: left bottom;
          transform-origin: left bottom;
  -webkit-transform: skew(45deg);
          transform: skew(45deg);
  position: absolute;
  background: #6b7684;
  right: -100px;
  left: 100px;
  content: "";
  bottom: 0;
  top: 0;
}

.bottom:after {
  -webkit-transform-origin: right bottom;
          transform-origin: right bottom;
  background: var(--background);
  right: 100px;
  left: -100px;
}
@media screen and (min-width: 800px) {
  nav {
    position: relative;
    -webkit-transition: none;
    transition: none;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    background: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
@media (min-width: 800px) {
  .logo {
    display: none;
  }
}
@media (min-width: 800px) {
  .logoM {
    justify-content: flex-start;
    display: flex;
  }
  .logoG {
    justify-content: flex-end;
    display: flex;
  }
}
@media (max-width: 800px) {
  .logoM {
    display: none;
  }
  .logo1 {
    margin-top: 86px;
  }
  .container1 {
    justify-content: space-evenly;
  }
}

